import React, { useState } from "react";
import { Button } from "@mui/material";

import PopUpDemo from "../../components/PopUpDemo";

import EngageMain from "../assets/engage_mobile.svg";
import EngageMainMd from "../assets/engage.svg";
import AutopilotImg from "../assets/autopilot.svg";
import MarketingImg from "../assets/marketing.svg";
import SupportImg from "../assets/support.svg";

const Engage = ({ isMobile, className }) => {
  const [demoModal, setDemoModal] = useState(false),
    openDemoModal = () => setDemoModal(true),
    closeDemoModal = (event, reason) => {
      setDemoModal(false);
    };

  return (
    <section className={className}>
      <div className="content">
        <div className="intro">
          {!isMobile && (
            <div className="image">
              <img
                src={EngageMainMd}
                alt="Feature-Engage"
                className="feature-image"
                height={543}
                width={476}
              />
            </div>
          )}
          <div className="data">
            <div className="description">
              <span className="badge">{"ENGAGE"}</span>
              <h2 className="title">
                {"WhatsApp ChatBot for business of all sizes."}
              </h2>
              {isMobile && (
                <div className="image">
                  <img
                    src={EngageMain}
                    alt="Feature-Engage"
                    className="feature-image"
                    height={424}
                    width={326}
                  />
                </div>
              )}
              <p className="info">
                {
                  "Enable more convenience to your customers on a platform they love - WhatsApp. Communicate better and experience a surge in growth."
                }
              </p>
              <Button variant="contained" href="/engage" className="action">
                {"Explore"}
              </Button>
            </div>
          </div>
        </div>
        <div className="features">
          <div className="block">
            <div className="block-image">
              <img src={SupportImg} alt="Support 24 x 7" />
            </div>
            <div className="info">
              <h3>{"Supercharge Support"}</h3>
              <p>{"Be available 24 x 7"}</p>
              <p>{"Increase efficiency, reply instantly"}</p>
            </div>
          </div>
          <div className="block">
            <div className="block-image">
              <img
                src={AutopilotImg}
                alt="Autopilot Sales"
                className="block-image"
              />
            </div>
            <div className="info">
              <h3>{"Autopilot Sales"}</h3>
              <p>{"Sell more with 80% less manpower."}</p>
              <p>{"Focus on qualified customers only"}</p>
            </div>
          </div>
          <div className="block">
            <div className="block-image">
              <img
                src={MarketingImg}
                alt="Maximize Marketing"
                className="block-image"
              />
            </div>
            <div className="info">
              <h3>{"Maximize Marketing"}</h3>
              <p>{"Reach new audience faster and enhance awareness."}</p>
              <p>{"Personalized messages for each customer."}</p>
            </div>
          </div>
        </div>
        <div className="cta">
          <div className="header">
            <h4>
              {
                "Automate your business with WhatsApp and reach 2.2 Billion customers."
              }
            </h4>
          </div>
          <div className="actions">
            <Button variant="outlined" href="/engage" className="explore">
              {"Explore"}
            </Button>
            <Button
              variant="contained"
              onClick={openDemoModal}
              className="demo"
            >
              {"Demo"}
            </Button>
          </div>
        </div>
      </div>
      <PopUpDemo open={demoModal} onClose={closeDemoModal} />
    </section>
  );
};

export default Engage;
